.homepage {
    height: calc( 100vh - 95px);
}

.backgroundimageone {
    margin-top: 95px;
    background-image: url("../assets/yugioh-banner-top.png");
    height: 40%;
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
    position: relative;
}

.backgroundimagetwo {
    background-image: url("../assets/yugioh-banner-bot.png");
    height: 40%;
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
    position: relative;
}

.backgroundimagecover {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgb(0, 0, 0, 0.75);
}

.coverarea {
    height: 100%;
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    align-items: center;
}

.cover {
    font-size: 4rem;
    font-weight: 900;
}

.covertext {
    font-size: 2.8rem;
    font-weight: 500;
}

.break {
    height: calc(20%);
    background-color: rgb(30, 36, 33);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-shadow: rgba(0, 0, 0, 0.9) 0px 3px 8px;
    border-top: 2px solid rgb(228, 228, 228);
    border-bottom: 2px solid rgb(228, 228, 228);
}